"use client";

// Hero section inspired by the provided design
export default function HeroSplit() {
  return (
    <section className="relative w-full h-[90vh] flex overflow-hidden">
      {/* Left: For Her */}
      <div className="flex-1 relative flex items-end justify-center bg-gradient-to-br from-orange-400 via-orange-600 to-amber-900">
        <img
          src="/images/for-her.jpg"
          alt="For Her"
          className="absolute inset-0 w-full h-full object-cover opacity-80 mix-blend-multiply"
        />
        <span className="absolute bottom-8 left-8 text-white text-lg font-semibold tracking-widest uppercase drop-shadow-lg">For Her</span>
      </div>
      {/* Right: For Him */}
      <div className="flex-1 relative flex items-end justify-center bg-gradient-to-tl from-gray-900 via-gray-700 to-gray-500">
        <img
          src="/videos/3434390639380735271.mp4"
          alt="For Him"
          className="absolute inset-0 w-full h-full object-cover opacity-80 mix-blend-multiply"
        />
        <span className="absolute bottom-8 right-8 text-white text-lg font-semibold tracking-widest uppercase drop-shadow-lg">For Him</span>
      </div>
      {/* Centered Headline Overlay */}
      <div className="absolute inset-0 flex flex-col items-center justify-center z-10 pointer-events-none">
        <div className="backdrop-blur-sm bg-black/30 px-8 py-4 rounded-xl">
          <h1 className="text-5xl md:text-7xl font-extrabold uppercase tracking-tight text-white text-center leading-tight drop-shadow-xl" style={{fontFamily: 'Oswald, Bebas Neue, Montserrat, sans-serif'}}>Indulge<br />Your Senses</h1>
        </div>
      </div>
    </section>
  );
}
